<template>
  <div class="home">
    <div class="top">
        <!-- 导航栏 -->
      <div class="navbar">
        <div class="add" @click="goAdd">
          +
        </div>
        <div><van-search @click="goSearch" v-model="value" placeholder="请输入要搜索的菜" /></div>
        <div class="classify-box" @click="goClassify"><img class="auto-img" src="../assets/icon/classify.png" alt=""></div>
        <div class="info-box">
          <img class="auto-img" src="../assets/icon/info.png" alt="">
        </div>
      </div>
      <!-- 标签栏 -->
      <div class="tabs">
        <van-tabs v-model="active" color="#fac438" line-height="5px" line-width="30px">
          <van-tab v-for="(item, index) in tab" :key="index" :title="item.title" :to="{name: item.routerName}" ></van-tab>
        </van-tabs>
      </div>
    </div>
    <div class="route">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Home',
    data() {
      return {
        images: [],
        value: '',
        active: 1,
        tab: [{
          title: '关注',
          routerName: 'Follow'
        },{
          title: '推荐',
          routerName: 'Recommend'
        },{
          title: '笔记',
          routerName: 'Notes'
        },{
          title: '视频',
          routerName: 'Video'
        },{
          title: '活动',
          routerName: 'Activity'
        }]
      }
    },
    created() {
      let routeName = this.$route.name;
      for(let i = 0; i < this.tab.length; i++) {
        if (this.tab[i].routerName === routeName) {
          this.active = i;
        }
      };

      
    },
    methods: {
      // 跳转到分类页面
      goClassify() {
        this.$router.push({name: 'Classify'});
      },

      // 跳转到搜素页面
      goSearch() {
        this.$router.push({name: 'Search'});
      },

      // 跳转到添加页面
      goAdd() {
        this.$router.push({name: 'Add'});
      }
    }
    
  }
</script>

<style lang="less" scoped>
  .home {
    // 导航栏   
    .top {
      position: fixed;
      top: 0;
      left: 0;
      height: 100px;
      width: 100%;
      z-index: 99;
      background-color: #f5f5f5;
      margin-bottom: 1px solid #000;
      overflow: hidden;
      .navbar {
        width: 355px;
        height: 55px;
        padding: 0 10px;
        display: flex;
        background-color: #fff;
        justify-content: space-between;
        align-items: center;
        .add {
          width: 30px;
          height: 30px;
          line-height: 30px;
          border-radius: 50%;
          text-align: center;
          background-color: #facc30;
          font-size: 24px;
        }
        .classify-box {
          width: 30px;
          height: 30px;
        }
        .info-box {
          width: 30px;
          height: 30px;
        }
      }
    }
   
    
  }
  /deep/.van-tab--active{
    font-size: 18px;
    font-weight: bolder;
  }
</style>
